import { getRandomBetween, getOrCreateImgPath } from '../library';
export default function populateObfBlocks() {
    const divs = document.getElementsByClassName('obfuscated-text-container');
    for (let i = 0; i < divs.length; i++) {
        let div = divs[i];
        let min = parseInt(div.dataset.minlength);
        let max = parseInt(div.dataset.maxlength);
        let amt = getRandomBetween(min, max);
        console.log("Generating " + amt + "/" + min + "-" + max + " precursor chars to fill div #" + i);
        let n = 0;
        for (let k = 0; k < amt; k++) {
            let num = getRandomBetween(0, 63);
            addChar(div, num);
            n++;
            if (Math.random() < 0.175 && n >= 2) {
                addChar(div, 64);
                n = 0;
            }
        }
    }
}
function addChar(div, num) {
    let img = document.createElement("img");
    img.className = "precursor-char";
    let name = "lorescript_" + num;
    img.src = getOrCreateImgPath(name, "precursor");
    div.appendChild(img);
}
